/* Desktop */
.about {
    margin-bottom: 2.5rem;
    margin-top: 2.5rem;
    padding: 5rem 0;
    background-color: var(--bg-light2);
    scroll-margin-top: 5.5rem;
    display: flex;
    justify-content: space-evenly;
    position: relative;
    overflow: hidden;
}

.title {
    text-align: center;
    font-weight: 800;
    font-size: 2rem;
    margin-bottom: 16px;
    color: #333;
    position: relative;
}

.title hr {
    width: 100px;
    padding: 0 30px;
    margin: 0 auto;
    border: 2px solid black;
    margin-top: 1rem;
    position: relative;
    z-index: 2;
}

.about span {
    font-weight: 600;
    letter-spacing: 2px;
    font-size: 1.5rem;
}

.about-img {
    position: relative;
    overflow: hidden;
    border-radius: 13px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.about-img::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    transform: rotate(5deg);
    width: 100%;
    height: 100%;
    z-index: 1;
}

.about-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
    z-index: 2;
}

.about-content {
    font-size: 2rem;
    position: relative;
    z-index: 2;
}

.about-content p {
    color: black;
    font-size: 1.2rem;
    font-weight: 500;
}

.border {
    position: relative;
    display: flex;
    width: 200px;
    height: 90px;
    text-align: center;
    font-size: 2rem;
    margin-bottom: 5rem;
    justify-content: center;
    align-items: center;
    border: 2px solid black;
    position: relative;
}

.border::before {
    content: '';
    position: absolute;
    bottom: -13px;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 0;
    border-left: 0px solid transparent;
    border-right: 10px solid transparent;
    border-top: 13px solid black;
}

/* Tablet */
@media screen and (max-width: 768px) {
    .about-img img {
        width: 100%;
        height: auto;
    }

    .about-content {
        text-align: center;
    }

    .border {
        width: 100%;
    }
}

/* Mobile */
@media screen and (max-width: 480px) {
    .about {
        padding: 3rem 0;
    }

    .title {
        font-size: 1.8rem;
    }

    .about-content p {
        font-size: 1rem;
    }
}
