/* Portfolio.module.css */

.portfolio {
    padding: 2.5rem 0;
}

.title h2 {
    text-align: center;
    font-size: 2rem;}

.cards {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5rem; /* Boşlukları küçült */
    margin: 2rem 5vw; /* Margin'ları düzenle */
    padding: 0;
    list-style: none;
}

.cards li {
    width: 100%;
    overflow: hidden;
    border-radius: 15px; /* Kart köşe yuvarlama */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
    transition: 0.3s ease-in-out;
}

.cards li:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
}

.cards img {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}

.cards .card-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-family: 'Poppins', sans-serif;
    background-color: rgba(255, 255, 255, 0.9);
    color: #333;
    z-index: 1;
    transform: translateY(100%);
    transition: 0.3s ease-in-out;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
}

.cards li:hover .card-overlay {
    transform: translateY(0);
}

.cards .card-header {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 1em; /* Padding'i düzenle */
    background-color: rgba(255, 255, 255, 0.9);
    transform: translateY(0);
    transition: 0.3s ease-in-out;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
}

.cards li:hover .card-header {
    transform: translateY(0);
}

.cards .card-content a {
    background: linear-gradient(45deg, #3498db, #6f7bff);
    color: #fff;
    font-weight: 600;
    padding: 10px 16px;
    border-radius: 10px;
    letter-spacing: 1px;
    font-size: 1rem;
    transition: 0.5s;
    text-decoration: none;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
}

.cards .card-content a:hover {
    background: linear-gradient(45deg, #2980b9, #6f7bff);
    letter-spacing: 2px;
}

.cards .card-title {
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 12px;
    z-index: 2;
    color: #333;
}

.cards .card-description {
    font-size: 1rem;
    padding: 0 1rem 1rem; /* Padding'i düzenle */
    margin: 0;
    overflow: hidden;
    color: #555;
}

@media screen and (max-width: 600px) {
    .cards {
        grid-template-columns: 1fr; /* Mobil ekranlarda sütun sayısını bir yap */
        gap: 1rem; /* Boşluğu azalt */
    }
}
