.list2 {
  margin: 16px auto;
  width: 100%;
  max-width: 650px;
  display: flex;
  align-items: center;
  justify-content:flex-start;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
  transition: 0.3s ease-in-out;
  overflow: hidden;
  position: relative; /* Added position relative for absolute positioning */
}

.list2:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
}

.school-pos {
  justify-content: center;
  align-items: center;
}
.school-logo {
  height: auto;
  width: 150px;
  object-fit: cover;
  margin-right: 27px;
}

.position2 {
  font-weight: 700;
  font-size: 1.5rem;
  margin-bottom: 18px;
  color: black;
}

.position2 .et {
  font-weight: 500;
  margin-bottom: 27px;
}

.position2 .et .school {
  font-weight: 700;
  font-size: 1.5rem;
  color: black;
}

.position2 .work2 {
  font-weight: 500;
  font-size: 1.2rem;
}
.position2 .time {
  font-size: 1.1rem;
  font-weight: 500;
}



.position2 .work2 {
  width: 100%;
  font-weight: 400;
  color: black;
}

.position2 .field {
  font-weight: 600;
  font-size: 1.5rem;
}
.experience2 {
  margin-top: 16px;
  margin-bottom: 16px;
  position: relative; /* Added position relative for absolute positioning */
}

.title2 {
  font-weight: 800;
  font-size: 2rem;
  margin-bottom: 2.5rem;
  margin-top: 2.5rem;
  text-align: center;
}

.details2 {
  margin: 0 auto;
  width: 100%;
}

.details2 ul {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-left: 20px; /* Kutucukların sol tarafındaki mesafe */
}

.timeline-container {
  position: absolute;
  left: -10px; /* Adjusted left position for better alignment */
  top: 0;
  height: 100%;
}

.timeline {
  width: 10px; /* Çubuğun genişliği */
  height: 100%;
  background-color: #d8bb3d; /* Çubuğun rengi */
  transform-origin: top; /* Başlangıç noktasını yukarıda belirt */
}

@media screen and (max-width: 768px) {
  .list2 {
    flex-direction: column;
    align-items: flex-start;
  }

  .details2 {
    width: 100%;
  }

  .school-logo {
    max-width: 100%;
    height: auto;
    margin-right: 0; /* Remove the margin on smaller screens */
    margin-bottom: 10px; /* Add margin to push the logo below the text */
  }
}
