.list {
  margin: 16px auto;
  width: 100%; /* Adjusted width for larger screens */
  max-width: 650px;
  display: flex;
  align-items: center; /* Center elements horizontally on larger screens */
  justify-content: flex-start;
  padding: 16px; /* Slightly reduced padding for a more compact look */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
  transition: 0.3s ease-in-out;
  position: relative;
  overflow: hidden;
}

.list .company-logo {
  height: auto;
  width: 100px;
  object-fit:cover;
  margin-right: 27px;
}


.list:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
}

.position {
  font-weight: 700;
  font-size: 1.5rem; /* Further reduced font size for smaller screens */
  margin-bottom: 12px; /* Adjusted margin for better spacing */
}

.position a {
  font-weight: 500;
  text-decoration: none;
}

.position span {
  font-weight: 500;
}

.position .time {
  font-size: 1.1rem; /* Further reduced font size for smaller screens */
  font-weight: 500;
}

.position .company {
  font-weight: 700;
  font-size: 1.5rem;

}

.position .ta {
  font-size: 1.2rem; /* Further reduced font size for smaller screens */
  font-weight: 500;
}

.work {
  font-size: 1.2rem; /* Further reduced font size for smaller screens */
  width: 100%;
  font-weight: 400;
  margin-top: 36px;
  margin-bottom: 18px; /* Adjusted margin for better spacing */
}

.experience {
  margin-top: 12px; /* Adjusted margin for better spacing */
  margin-bottom: 12px; /* Adjusted margin for better spacing */
}

.title {
  font-weight: 800;
  font-size: 2rem; /* Slightly reduced font size for smaller screens */
  margin-bottom: 2.5rem; /* Adjusted margin for better spacing */
  margin-top: 2.5rem; /* Adjusted margin for better spacing */
  text-align: center;
}

.details {
  margin: 0 auto;
  position: relative;
}

.details ul {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-left: 20px; /* Adjusted margin for better spacing */
}

@media screen and (max-width: 768px) {
  .list {
    width: 100%;
    max-width: none; /* Reset max-width for smaller screens */
    align-items: flex-start; /* Align elements to the left on smaller screens */
  }

  .details {
    width: 100%;
  }
}
