/* Cards.module.css */

.card {
    position: relative;
    display: block;
    height: 100%;
    width: 100%;
    border-radius: 15px; /* Kartın köşe yuvarlama miktarını düşür */
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
    transition: 0.3s ease-in-out;
}

.card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}

.card-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-family: 'Poppins', sans-serif;
    background-color: rgba(255, 255, 255, 0.9);
    color: #333;
    z-index: 1;
    transform: translateY(50%);
    transition: 0.3s ease-in-out;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
}

.card:hover .card-overlay {
    transform: translateY(0);
}

.card-header {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px 15px; /* Padding miktarını azalt */
    background-color: rgba(255, 255, 255, 0.9);
    transform: translateY(0);
    transition: 0.3s ease-in-out;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
}


.card:hover .card-header {
    transform: translateY(0);
}

.card-content a {
    background-color: #333;
    color: #fff;
    font-weight: 600;
    padding: 10px 15px; /* Padding miktarını azalt */
    border-radius: 10px;
    letter-spacing: 1px;
    font-size: 0.9rem; /* Yazı boyutunu düşür */
    transition: 0.5s;
    text-decoration: none;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
}

.card-content a:hover {
    background-color: #333;
    letter-spacing: 2px;
}

.card-title {
    font-size: 1rem;
    font-weight: 500;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 8px; /* Alt boşluğu azalt */
    z-index: 3;
    color: #333;
}

.card-type {
    font-size: 1rem;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 8px; /* Alt boşluğu azalt */
    z-index: 3;
    color: #333;
}



.card-description {
    font-size: 0.9rem; /* Yazı boyutunu düşür */
    padding: 1.5rem; /* Padding miktarını azalt */
    margin: 0;
    overflow: hidden;
    color: #555;
}

@media screen and (max-width: 768px) {
    .cards {
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); /* Kart genişliklerini ayarla */
        margin: 2rem 5vw; /* Kenar boşluğunu azalt */
    }
}
